@import '../../../../sourses/styles/variables.scss';

.container {
  max-height: 300px;
  overflow-y: scroll;

  .list {
    position: relative;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 20px;

    max-width: 300px;
    padding: 20px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-radius: 5px;

    &:hover {
      box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
    }

    .item {
    }
  }

  .iconEditContainer {
    position: absolute;
    top: 0%;
    right: 0%;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 50px;
    height: 50px;
    border-radius: 4px;
    fill: $brandColor;

    &:hover {
      cursor: pointer;
      fill: #67b4fb;
    }
    .icon {
      width: 20px;
      height: 20px;
    }
  }
  .iconBinContainer {
    position: absolute;
    top: 0%;
    right: 20%;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 50px;
    height: 50px;
    border-radius: 4px;
    fill: $brandColor;

    &:hover {
      cursor: pointer;
      fill: #dd1818;
    }
    .icon {
      width: 20px;
      height: 20px;
    }
  }
}
